@import url('https://fonts.googleapis.com/css?family=Montserrat:500&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Monserrat', sans-serif;
}

html, body {
    height: 100%;
    /*width: 100%;*/
    color: #222;
}

body {
    /*font-size: 1.3rem;*/
    background: #f9f9f9;
    /*min-width: 1028px;*/
}

a {
    text-decoration: none;
}

.myheader {
    padding: -100px;
}

.navbar:hover {
    cursor: pointer;
}

.navbar {
    padding-bottom: 0.8rem;
}

.navbar-nav li {
    padding-right: 20px;
}

.nav-link {
    font-size: 1.1em !important;
}

.padding {
    padding-bottom: 2rem;
}

.welcome {
    padding-top: 2rem;
}

.welcome hr {
    margin-top: 0.3rem;
    margin-bottom: 1rem;
    text-align: right;
}

img {
    width: 100%;
}

.proj {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    background: #f1f1f1;
    margin-bottom: 2rem;
}

.proj h3 {
    margin-bottom: 2rem;
}

.proj img {
    height: 400px;
}

.proj > div {
    padding: 2rem;
}

.proj:nth-child(even) img {
    order: 2;
}

