.link-button {
    color: #0d0d0d;
    border: 2px solid #1AEFC6;
    padding: 10px 20px;
    margin: 80px;
    border-radius: 24px;
    cursor: pointer;
    transition: 0.5s;
}

.link-button:hover {
    background: #1AEFC6;
    transform: scale(1.25);
}

.expertise {
    padding-top: 100px;
}

.languages {
    padding: 50px;
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.contactme {
    padding-top: 140px;
    padding-bottom: 50px;
    text-align: center;

}

.profilepic {
    height: 267px;
    width: 225px;
}

.mojFooter{
    background-color: #f8f8f8;
    color: #0f0f0f;
    border-top: 1px solid #e7e7e7;
    margin-bottom: 0;
}

.bottom-footer{
    border-top: 1px solid #0f0f0f;
    margin-top: 0px;
    padding-top: 7px;
    color: #0f0f0f;

}

.footer-nav li{
    display: inline-flex;
    padding: 0 40px;
    color: grey;
}

@media(max-width:700px) {
    .proj {
        display: block;
    }
}

@media (max-width: 992px) {
    .header {
        background-size: cover;
    }
}
@media (max-width: 768px) {
    .header {
        background-size: auto;
    }
}
@media (max-width: 576px) {
    .header {
        background-size: auto;
    }
}

@media (max-width: 480px)  {
    .header {
        background-size: auto;
    }
}


